.header {
  position: relative;
}

.contenidog-header {
  position: absolute;
}
.contenidog-header .contenido-header {
  z-index: 10;
  position: relative;
}

.contenidog-header {
  height: 100%;
  width: 100%;
}

.contenido-header {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.contenido-header .imagen-personal img {
  border-radius: 150px;
}
.contenido-header h2 {
  color: white;
}
.contenido-header h2 span {
  color: rgb(56, 196, 243);
  animation: parpadeoNombre 2s linear infinite;
}
.contenido-header p {
  color: white;
  font-weight: 700;
}

.header_img {
  background-image: url("/build/img/imgprogramacion.webp");
  background-size: cover;
  height: 950px;
}

@keyframes parpadeoNombre {
  0%, 10%, 15%, 20%, 35%, 40%, 50%, 55%, 58%, 59% {
    opacity: 1;
  }
  30%, 45%, 0%, 90%, 100% {
    opacity: 0;
  }
}
/** Media Queries **/
ul {
  list-style: none;
  padding: 0;
  width: 100%;
  text-align: center;
}
ul li {
  padding: 0.7rem 0;
}
ul li:hover {
  border-left: 3px solid #3232f6;
  transition: border 0.5s;
}

.contenido-navegacion {
  position: absolute;
  z-index: 10000;
  width: 100%;
  height: 100%;
  /*NAVEGACION*/
  /*TABLET NAVEGACION*/
}
.contenido-navegacion .navegacion {
  transition: all 0.5s ease-in-out;
  position: relative;
  background-color: rgba(28, 28, 28, 0.5294117647);
  visibility: hidden;
  max-width: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
}
.contenido-navegacion .navegacion a {
  color: white;
  font-weight: 600;
  padding: 1rem 0;
  letter-spacing: 0.5px;
  font-size: 20px;
}
.contenido-navegacion .navegacion.visible {
  visibility: visible;
  opacity: 1;
  max-width: 100%;
  transition: all 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .contenido-navegacion .navegacion.visible {
    max-width: 300px;
  }
}

.toggle {
  position: absolute;
  top: 5%;
  left: 3%;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 25px;
}
.toggle:hover {
  cursor: pointer;
}
.toggle.novisible {
  visibility: hidden;
}

.close {
  position: absolute;
  background-color: transparent;
  color: white;
  border: none;
  font-size: 25px;
  top: 5%;
  left: 85%;
}
.close:hover {
  cursor: pointer;
}

.btn-dark {
  position: absolute;
  top: 5.5%;
  left: 10%;
  background-color: transparent;
  border: none;
  font-size: 25px;
  color: white;
}
.btn-dark:hover {
  cursor: pointer;
  color: black;
  text-shadow: -1px 0px 5px rgba(164, 164, 164, 0.872);
}

.sobre-mi {
  background-color: rgb(19, 19, 19);
  padding: 4rem 0;
}

.contenedorg-sobremi {
  position: relative;
  z-index: 99;
  height: 100%;
}

.contenedor-sobremi {
  height: 100%;
  width: 100%;
}
.contenedor-sobremi .grid-sobremi {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenedor-sobremi .grid-sobremi h2, .contenedor-sobremi .grid-sobremi p {
  color: white;
  padding: 0 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-sobremi .grid-sobremi {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenedor-sobremi .grid-sobremi h2, .contenedor-sobremi .grid-sobremi p {
    padding: 0;
    text-align: left;
  }
}

.sobremi-img {
  width: 350px;
}

/*PARTICULAS*/
#particles-js {
  position: absolute;
  background-color: rgb(19, 19, 19);
  height: 800px;
  width: 100%;
}

.sobremi-texto p {
  font-size: 17px;
}
.sobremi-texto p strong a {
  color: white;
}
.sobremi-texto ul {
  color: white;
  list-style: circle;
  padding: 0 3rem;
  text-align: left;
}
.sobremi-texto ul:hover {
  border: 1px solid white;
  cursor: pointer;
}
.sobremi-texto ul li {
  border-left: none;
  font-family: "Poppins", sans-serif;
}

.tecnologias {
  -webkit-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  -moz-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  padding: 5rem 0;
}
.tecnologias h2 {
  text-align: center;
  margin-top: 0;
  text-transform: uppercase;
}
.tecnologias .contenedor-vermas {
  margin: 7rem 0 0 0;
  text-align: center;
}
.tecnologias .contenedor-vermas a {
  color: black;
  padding: 1.4rem 2.5rem;
  border: 2px solid blue;
  color: black;
}
.tecnologias .contenedor-vermas a:hover {
  background-color: blue;
  transition: all 0.5s;
  color: white;
  font-size: 2.1rem;
}

.contenedor-tecnologias {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenedor-tecnologias {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 5rem;
  }
}
.contenedor-tecnologias div:nth-of-type(3) {
  border-radius: 20px;
}

.logos {
  width: 180px;
  margin: 3rem auto;
}
@media (min-width: 768px) {
  .logos {
    width: 250px;
  }
}
.logos:hover {
  transform: rotate(10deg) scale(1);
  transition: transform 0.8s;
  cursor: pointer;
}
.logos img {
  border-radius: 30px;
}

.footer {
  background: rgb(1, 7, 29);
  background: linear-gradient(270deg, rgb(1, 7, 29) 0%, rgb(0, 1, 10) 100%);
  padding: 1rem;
  text-align: center;
}
.footer p {
  color: white;
  font-size: 15px;
  margin: 0;
  animation: parpadeo 2s linear infinite;
  transition: 0.5s ease;
}
@media (min-width: 768px) {
  .footer p {
    font-size: 20px;
  }
}

@keyframes parpadeo {
  0%, 41%, 45%, 47%, 100% {
    opacity: 1;
    text-shadow: 0px 0px 10px rgb(0, 76, 243);
  }
  42%, 44%, 46%, 48%, 49.5%, 50% {
    opacity: 0;
    text-shadow: none;
  }
}
.proyectos {
  padding: 3rem 0;
}

.proyecto {
  padding-bottom: 2rem;
  margin: 1rem 0;
  -webkit-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  -moz-box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  box-shadow: 0px 0px 5px 0px rgb(176, 176, 176);
  border-radius: 18px;
}
.proyecto .img-proyecto img {
  border-radius: 15px;
}

.contenedorg-proyectos h2 {
  text-align: center;
}

.contenedor-proyectos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenedor-proyectos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
  }
}

.titulo-proyecto h3 {
  text-align: center;
  margin-bottom: 0;
}
.titulo-proyecto h3 a {
  color: rgba(0, 0, 0, 0.856);
}

.contenedor-ver {
  margin: 1rem 0;
  text-align: center;
}
.contenedor-ver a {
  color: rgba(0, 0, 0, 0.856);
  border: 2px solid black;
  padding: 1.2rem;
}
.contenedor-ver a:hover {
  background-color: black;
  color: white;
  transition: all 0.3s ease-in-out;
}

.texto-proyecto {
  padding: 0.3rem;
}
.texto-proyecto p {
  font-size: 13px;
  padding: 0 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .texto-proyecto p {
    padding: 0 1rem;
    font-size: 15px;
  }
}

.linea-divisora {
  width: 100%;
  text-align: center;
  border: 0.5px solid rgba(0, 0, 0, 0.332);
}

.contenedor-form {
  height: 93.5%;
  position: absolute;
  width: 100%;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.6549019608);
}

.form-contacto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  height: 100%;
  width: 80%;
  font-weight: 600;
}
.form-contacto label {
  display: block;
  font-family: "Poppins", sans-serif;
  margin: 0.5rem 0;
}
.form-contacto input:not([type=submit]) {
  width: 100%;
  height: 30px;
  padding: 10px 10px;
}
.form-contacto input[type=submit] {
  display: block;
  background-color: transparent;
  border: 2px solid rgb(53, 151, 237);
  outline: none;
  padding: 1rem;
  margin: 2rem 0;
  color: white;
  font-weight: 700;
}
.form-contacto input[type=submit]:hover {
  background-color: rgb(53, 151, 237);
  padding: 1.2rem;
  cursor: pointer;
  transition: all 0.5s ease;
}
.form-contacto input, .form-contacto textarea, .form-contacto select {
  color: white;
  background-color: black;
  border: 1px solid rgb(53, 151, 237);
  border-radius: 3px;
  outline: 0;
}
.form-contacto select {
  width: 100%;
  height: 35px;
}
.form-contacto textarea {
  width: 100%;
}
.form-contacto fieldset, .form-contacto legend {
  color: rgb(217, 228, 240);
  font-size: 19px;
}

.imagen-contacto {
  background-image: url("/build/img/imagen-contacto.webp");
  height: 900px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.dark-mode .close {
  color: black;
  text-shadow: 0.5px 0.5px 4px rgba(255, 255, 255, 0.923);
}
body.dark-mode .btn-dark {
  color: black;
  text-shadow: 0.1px 0.5px 4px rgba(255, 255, 255, 0.923);
}
body.dark-mode .btn-dark:hover {
  color: white;
}
body.dark-mode .tecnologias {
  background: rgb(18, 18, 18);
}
body.dark-mode .tecnologias h2 {
  color: white;
}
body.dark-mode .contenedor-vermas a {
  color: rgb(17, 17, 17);
}
body.dark-mode .tecnologias {
  -webkit-box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
  -moz-box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
  box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
}
body.dark-mode .tecnologias a {
  color: white;
}
body.dark-mode .proyectos {
  -webkit-box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
  -moz-box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
  box-shadow: 1px 13px 22px -8px rgb(176, 176, 176);
  background-color: rgb(19, 19, 19);
}
body.dark-mode .proyectos h3, body.dark-mode .proyectos p {
  color: white;
}
body.dark-mode .proyectos .linea-divisora {
  border-color: rgba(255, 255, 255, 0.771);
}
body.dark-mode .proyectos a {
  filter: invert(100%);
}
body.dark-mode .proyectos h2 {
  color: white;
}
body.dark-mode .footer {
  box-shadow: 0 0 5px 0 #b0b0b0;
  background: black;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  height: 100%;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 510;
}

p, a {
  font-family: "Poppins", sans-serif;
}

.p-translate {
  text-align: center;
  font-size: 15px;
  cursor: pointer;
}

.contenedor-nav {
  padding: 1.2rem 0;
  background: rgb(1, 7, 29);
  background: linear-gradient(270deg, rgb(1, 7, 29) 0%, rgb(0, 1, 10) 100%);
  text-align: center;
}
.contenedor-nav a {
  color: white;
  font-size: 2rem;
}

.hover-underline-animation {
  display: inline-block;
  position: relative;
  color: #0087ca;
}

.hover-underline-animation::after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #12f6df;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.hover-underline-animation:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.hover-underline-animation:hover {
  font-size: 2.1rem;
  transition: font-size 0.5s ease-out;
}

.gulp {
  width: 100px;
}